import React, {useState}  from "react"
import { Link } from "gatsby"


const Header = () => {
  const [isMain,setIsMain]=useState(true)
  const [isAbout,setIsAbout]=useState(false)
  const [isBlog,setIsBlog]=useState(false)
  const [isContact,setIsContact]=useState(false)

  const onClickBlog = ()=>{
    console.log('sth')

    setIsMain(false)
    setIsBlog(true)
    console.log(isMain)
  }

  if(isMain){
    return(
      <header style={{ padding:'5vh 5vw',width:'90vw',position:'absolute', top:'87vh', left:'0', transition: 'all .2s', color:'white'}}>
      <div>
        <Link to="/" style={{ position: 'relative',  textDecoration: `none`, }}>
          <div style={{ filter: 'opacity(.6) drop-shadow(0 0 0 white)', position: 'absolute', width: '26px', height: '26px', color:'white',  backgroundImage: 'url(https://user-images.githubusercontent.com/71132893/103412811-4ba46f80-4bba-11eb-94b9-454fc8d16d8d.png)',backgroundSize: 'contain', backgroundRepeat: 'no-repeat'}}></div>
          <div style={{ position: 'absolute', width: '90px', left: '30px', color:'white', fontWeight:'600'}}>Ho'space</div>
        </Link>
      </div>
      <ul style={{ margin: '0', padding: '0', listStyle: `none`, float: `right` }}>
        <Link style={{ float: `left`,marginRight: `1rem`,textDecoration: 'none' ,color:'white'}} to="/about/">About</Link>
        <Link onClick={onClickBlog} style={{ float: `left`,marginRight: `1rem`,textDecoration: 'none' ,color:'white'}} to="/blog/">Blog</Link>
        <Link style={{ float: `left`,marginRight: `1rem`,textDecoration: 'none' ,color:'white'}} to="/contact/">Contact</Link>
      </ul>
    </header>
    )
  }else{
    return (
      <header style={{ position: 'relative', margin: `0 auto`, maxWidth: 1280, padding: `1.45rem 1.0875rem`, marginBottom: `2rem`, }}>
        <div style={{ float: 'left', margin: 0 }}>
          <Link to="/" style={{ position: 'relative', color: `black`, textDecoration: `none`, }}>
            <div style={{ position: 'absolute', width: '26px', height: '26px', backgroundImage: 'url(https://user-images.githubusercontent.com/71132893/103412811-4ba46f80-4bba-11eb-94b9-454fc8d16d8d.png)', backgroundSize: 'contain', backgroundRepeat: 'no-repeat' }}></div>
            <div style={{ position: 'absolute', width: '90px', left: '30px' }}>Ho'space</div>
          </Link>
        </div>
        <ul style={{ margin: '0', padding: '0', listStyle: `none`, float: `right` }}>
          <Link style={{ float: `left`,marginRight: `1rem`,textDecoration: 'none' ,color:'black'}} to="/about/">About</Link>
          <Link onClick={onClickBlog} style={{ float: `left`,marginRight: `1rem`,textDecoration: 'none' ,color:'black'}} onClick={onClickBlog} to="/blog/">Blog</Link>
          <Link style={{ float: `left`,marginRight: `1rem`,textDecoration: 'none' ,color:'black'}} to="/contact/">Contact</Link>
        </ul>
      </header>
    )
  }
}



export default Header
